import React from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCol,  
  } from '@coreui/react'
  import CIcon from '@coreui/icons-react';
import avtar from '../../assets/img/c12.jpg';

export default function OrderCard(props) {
    return (
      <>
      {props.data.map((item) => (
            <CCard className="mx-10" style={{ boxShadow: "0px 0px 1px 1px #110730", borderRadius: "20px" }}>
            
              <CCardBody className="p-10" >
                <div className="Ordercard_body">
                  <img src={item.invoiceImage} />
                  <p>OrderID : {item.invoice_no}</p>
                </div>
                <p style={{ marginBottom: "0px" }}>Bill Amount: {item.invoiceAmount}</p>
                <p style={{ marginBottom: "0px" }}>Rewards: {}</p>
                <p style={{ marginBottom: "0px" }}>Expected Cashback Date: {}</p>
                <p style={{ marginBottom: "0px" }}><CIcon name="cilNotes" /> &nbsp;Bakery</p>
                <p style={{ marginBottom: "0px" }}><CIcon name="cilCalendar" />&nbsp; 22/05/2021</p><br />
              


              </CCardBody>
             
            </CCard>
             ))}
             </>
    )
}

