import React, { Component } from 'react';
import {
  CCol,
  CContainer,
  CRow,
  } from '@coreui/react'
import '../../scss/order.scss'
import "src/scss/profile.scss"
import axios from 'axios';
import { Base_URL, headers } from '../../CommonRoutes/Api';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import OrderCard from '../component/OrderCard';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';

class MyCashback extends Component {
  constructor(props) {
    super(props);
    this.state = {
       orderList: [],
       vid:"",
       blocking:false
  
    }
    this.changeStatus = this.changeStatus.bind(this);
  }


 componentDidMount(){

  if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
  {
    this.props.history.push("/");
  }
  
  let id=localStorage.getItem("user_id")
  this.setState({blocking:true})
  fetch(Base_URL + "invoices_by_status_U/pending/" + id).then((response) => {
    response.json().then((result) => {
        console.log(result.data)
        this.setState({ orderList: result,blocking:false})
        setTimeout(
          () => this.setState({ position: 1,blocking:false }), 
          3000
        );
        
       
    }) 
   
    
})  

 }
  

  changeStatus(e) { 
    let id=localStorage.getItem("user_id")
        this.setState({blocking:true})
  fetch(Base_URL + "invoices_by_status_U/" + e.target.value + "/" + id).then((response) => {
      response.json().then((result) => {
          console.log(result.data)
          this.setState({ orderList: result,blocking:false})
          setTimeout(
            () => this.setState({ position: 1,blocking:false }), 
            3000
          );
          
         
      }) 
     
      
  })  
  
 

}

  render() {
    return (
      <>
      <CContainer style={{ padding: "10px" }}>
          <CCol md="12" lg="12" xl="12" style={{marginTop:'-1rem',marginBottom:'2rem'}}>
       <FormControl style={{width:'100%'}}>
        <InputLabel id="demo-simple-select-label">Select Status</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"  selected
                       value={this.state.status}
                      onChange={this.changeStatus}
                      defaultValue="pending"
        
        >
          <MenuItem value="pending">Pending</MenuItem>
          <MenuItem value="accept">Accepted</MenuItem>
          <MenuItem value="reject">Rejected</MenuItem>
        </Select>
      </FormControl>
      </CCol>

        <CRow className="justify-content-center">
          <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
          {this.state.orderList.data && this.state.orderList.data.length > 0 ? (
              <OrderCard data={this.state.orderList.data} 
              handleStatus={this.handleStatus} />
            ) : (
              "No data found"
            )}
          </CCol>
        </CRow>
      </CContainer>
      <BlockUI blocking={this.state.blocking} />
      </>
    );
  }
}

export default MyCashback;